// Cadastro.jsx
import React from 'react';
import { Typography } from '@material-ui/core';

export default ({ data, pvalue }) => (
    <div id="end">
        <Typography component="h1" variant="h4" align="center">
            Tarefa concluda!
        </Typography>
        <table border='1'>
            <tbody>
                <tr>
                    <th>#</th>
                    <th>Score</th>
                </tr>
                {data.map((e, i) => (
                    <tr key={i}>
                        <td>{i + 1}</td>
                        <td>{(e.score * 100).toFixed(2)} pts</td>
                    </tr>
                ))}
                {/* <tr>
                    <td>p</td>
                    <td>{pvalue}</td>
                </tr> */}
            </tbody>
        </table>
    </div>
)



